.tab-container {
  overflow-x: auto;
  /* overflow-y: scroll; */
  white-space: nowrap;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.tble-hed-stick {
  position: sticky;
  top: 0;
}

.tab-container::-webkit-scrollbar {
  display: none;
}

.tab-row {
  display: flex;
}

.tab-row::-webkit-scrollbar {
  display: none;
}

.tab-item {
  padding: 7px 20px;
  margin-right: 10px;
  white-space: nowrap;
}

.hidescroll::-webkit-scrollbar {
  display: none !important;
}

/* background: linear-gradient(to top,#aed845,#9bd362); */
.task-card {
  background: linear-gradient(to right, #5ced73, #5ced73);
}


.hrul {
  border-right: 2px solid gray;
}

.rem1 {
  font-size: 1rem;
}

.card {
  box-shadow: 2px 2px 4px 0.2px rgb(211, 210, 210);
}

.cus-shadow {
  box-shadow: 2px 2px 4px 0.2px rgb(211, 210, 210) !important;
}

.cus-shadow:active {
  box-shadow: none !important;
}

.icon {
  width: 45px;
  height: 45px;
  background-color: #0d6efd;
  border-radius: 50%;
  font-size: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  box-shadow: inset 2px 2px 4px 0.2 black;
}

.sIcon {
  position: relative;
  top: -36px;
  left: 10px;
  padding: 0px;
}

.invoiceLogo {
  width: 100%;
  height: max-content;
  max-width: 100px;
  min-width: 70px;
}

.smallicon {
  width: 30px;
  height: 30px;
  background-color: #0d6efd;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  box-shadow: inset 2px 2px 4px 0.2 black;
}

.rounded-select-container {
  border-radius: 30px;
  padding: 8px 14px;
  /* background-color: #0d6efd; */
  background-color: #f2f5f8;
  margin: 2px 4px;
}

.rounded-select {
  outline: none;
  padding: 3px;
  background-color: #0d6efd;
  border: none;
  color: white;
}

.cus-card {
  box-sizing: border-box;
  margin: 15px 0px 0px 0px;
  border-radius: 15px;
  box-shadow: 2px 2px 4px 0.2px rgb(211, 210, 210);
  background-color: white;
  color: rgb(75, 67, 67);
  font-size: 16px;
}

.cus-badge {
  padding: 3px 9px;
  border-radius: 10px;
}

/* sales invoice */

.selectCard {
  cursor: pointer !important;
  transition: all 0.3s;
}

.selectCard:active {
  border: 1px solid blue;
}

.invoice-card {
  max-width: 100%;
}
.invoice-header {
  border-bottom: 1px solid #dee2e6;
}
.invoice-summary {
  border-top: 1px solid #dee2e6;
}


.sales-card {
  transition: all 0.3s ease-in-out;
  border-radius: 10px;
  overflow: hidden;
}

.sales-card:hover {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  transform: translateY(-4px);
}

.product-img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 8px;
  border: 1px solid #dee2e6;
}

.product-item {
  border-left: 3px solid #0d6efd;
  padding-left: 10px;
}

/* sales invoice */

.multiple-colors-gradient {
  background: linear-gradient(310deg, #b5cce6, #87CEEB, #D8BFD8, #b5e6dd);
}

.clock-btn {
  margin-top: 1em;
  text-align: center;
  text-transform: uppercase;
  background-color: #b5e6dd;
  border: none;
  border-radius: 50%;
  height: 110px;
  width: 110px;

}


.project-card {
  border-radius: 20px;
  padding: 10px 15px;
  border: 1px solid #e0dddd;
  background: rgba(255, 255, 255, 0.322);
  color: darkslateblue;
  margin-bottom: 19px;
  transition: all 0.3s;
  cursor: pointer;
}

.project-card:hover {
  box-shadow: 0px 0px 6px 0.2px rgb(161, 161, 161) !important;
}

.mnh {
  min-height: 6.8em !important;
  background-color: #00000007;
}

.under-blue:hover {
  border-bottom: #0d6efd solid 1px;
}

.chat-header {
  background-color: #E4959E;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 8px 16px;
}

.chat-body {
  padding: 8px 16px;
  height: 71vh;
  overflow: auto;
  background-color: #ADD7F6;
  display: flex;
  flex-direction: column;
}

.text-gold {
  color: #FFD700;
}

.messages {
  width: fit-content;
  max-width: 85%;
  border-radius: 8px;
  padding: 0.6em;
  margin: 10px 0px;
  font-size: 13px;
  background-color: #FCFCFC;
  box-shadow: 2px 2px 3px rgba(117, 117, 117, 0.582) !important;
}

.date-badge {
  text-align: center;
  width: 100%;
}

.message-right {
  margin-left: auto;
  background-color: #DCF8C6;
}

.message-left {
  margin-right: auto;
  background-color: #FCFCFC;
}

.chat-footer {
  background-color: #82c3f1;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.input-icon-container {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
}

.outline-none {
