.task-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}


.task-card {
  transition: all 0.3s;
}

.task-card:hover {
  box-shadow: 2px 2px 4px 0.2px rgb(211, 210, 210) !important;
  transform: translateY(-2px);
}

.cellHover {
  cursor: pointer;
  transition: all 0.3s;
}

.cellHover:hover {
  box-shadow: 2px 2px 4px 0.2px rgb(211, 210, 210) !important;
  transform: translateY(-2px);
}

.cellHover:active {
  box-shadow: none;

}

.cellHoverColor {
  cursor: pointer;
  transition: all 0.3s;
}

.cellHoverColor:hover {
  color: #0d6efd;
  font-weight: bold;
}

